<template>
  <div class="mb_controller" v-bind:style="epsColStyles">
    <div class="bg p-0 mb-2" style="width:100%;">
      <div class="col-md-12">
        <div class="row bg_header pt-3 mt-4 mb-3 d-flex justify-content-center align-items-center">
          <div class="rtblogo">
            <img style="width:100%;" src="../assets/top-menu/logo-rbh-go.png" />
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid mb-3">
      <div class="row searchinputstyle mx-2">
        <div class="searchicon d-flex justify-content-center align-items-center" style>
          <a href="#">
            <img src="../assets/top-menu/search-ico.png" />
          </a>
        </div>
        <!-- <div class="borderside d-flex justify-content-center align-items-start">
          <span>|</span>
        </div>-->
        <div class="inputbox pl-3 d-flex justify-content-center align-items-center">
          <input
            v-model="searchinput"
            placeholder="Search"
            style="width:100%;"
            @input="searchRecord"
          />
        </div>
        <!-- <div class="borderside d-flex justify-content-center align-items-start">
          <span>|</span>
        </div>-->
        <div
          class="closebutton d-flex justify-content-center align-items-center"
          @click="$router.push('/')"
        >
          <span class="closeicon">X</span>
          <span class="close_text">Close</span>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="row px-2">
        <div
          class="col-6 col-md-3"
          v-for="item in searchArr"
          :key="item.id"
          @click="goTo(item.path)"
        >
          <div class="item">
            <div class="item_img">
              <img v-bind:src="item.img_url" @error="imageLoadError($event)" />
            </div>
            <p class="items_title pl-1">
              <Trunquee :text=" item.title" />
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import Vue from "vue";
import noimage from "../assets/noimage.png";
export default {
  data: function() {
    return {
      searchinput: "",
      searchArr: [],
      noimage: noimage,
      epsColStyles: {},
      window: {
        height: 0
      }
    };
  },
  methods: {
    goTo(link) {
      this.$router.push(link);
    },
    imageLoadError(event) {
      event.target.src = noimage;
    },
    searchRecord() {
      const baseURI =
        "https://rtb.glueapi.io/v1/content?limit=1000&query=" +
        this.searchinput;

      try {
        this.$axios
          .get(baseURI)
          .then(response => {
            this.getResult(response.data.data);
          })
          .catch(error => {
            console.log(error);
          });
      } catch (e) {
        console.log(e);
      }
    },
    setInitial() {
      this.searchinput = "";
      this.searchRecord();
    },
    imgFiltering(item) {
      var imgurl;
      //check if
      var checkThumbnail =
        item.data && item.data.images && item.data.images.thumbnail;
      var checkTitle = item.data && item.data.images && item.data.images.title;
      var checkImgFeature =
        item.data && item.data.images && item.data.images.feature;

      if (checkThumbnail) {
        imgurl =
          "https://rtb-images.glueapi.io/500x0/" +
          item.data.images.thumbnail.path;
      } else {
        if (checkTitle) {
          imgurl =
            "https://rtb-images.glueapi.io/500x0/" +
            item.data.images.title.path;
        } else {
          if (checkImgFeature) {
            imgurl =
              "https://rtb-images.glueapi.io/500x0/" +
              item.data.images.feature.path;
          } else {
            imgurl =
              "https://cdn-rtb.glueapi.io/v1/content/" +
              item.id +
              "/image/300x0/images.title";
          }
        }
      }
      return imgurl;
    },
    getResult(value) {
      var store = [];
      var img_link;
      value.forEach(item => {
        if (
          (item.idSchema == "202") |
          (item.idSchema == "203") |
          (item.idSchema == "205") |
          (item.idSchema == "207") |
          (item.idSchema == "209")
        ) {
          var idSchema = item.idSchema;
          var id = item.id;
          var title = item.data.title;
          img_link = this.imgFiltering(item);

          var link;
          if (idSchema == "202") {
            //radio
            link = "/radio/" + item.id;
          } else if (idSchema == "203") {
            //pod
            link = "/podcastplayer?id=" + item.id;
          } else if (idSchema == "204") {
            //pod episode
            link = "/podcastplayer?id=" + item.id;
          } else if (item.idSchema == "205" || item.idSchema == "206") {
            //vod
            link =
              "/play?scheme=" +
              item.idSchema +
              "&p_type=true&id=" +
              item.id +
              "&eps_id=";
          } else if (item.idSchema == "205") {
            //vod
            link =
              "/play?scheme=" +
              item.idSchema +
              "&p_type=true&id=" +
              item.id +
              "&eps_id=";
          } else if (item.idSchema == "207") {
            //news
            link =
              "/play?scheme=" +
              item.idSchema +
              "&p_type=true&id=" +
              item.id +
              "&eps_id=";
          } else if (item.idSchema == "208") {
            //vod
            link =
              "/play?scheme=" +
              item.idSchema +
              "&p_type=false&id=" +
              item.id +
              "&eps_id=";
          } else if (item.idSchema == "209") {
            //news
            idSchema = "209";
            // console.log(JSON.stringify(item, null, 2));
            link = "/play?scheme=209&p_type=false&id=" + item.id + "&eps_id=";
          }

          store.push({
            idSchema: idSchema,
            id: id,
            title: title,
            path: link,
            img_url: img_link
          });
        }
      });

      this.searchArr = store;
    },
    handleResize() {
      this.window.height = window.innerHeight;
      var minHeight = this.window.height - 57 + "px";
      Vue.set(this.epsColStyles, "min-height", minHeight);
    }
  },
  mounted() {
    setTimeout(
      function() {
        this.setInitial();
      }.bind(this),
      200
    );
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  }
};
</script>
