<template>
  <div class="row pt-3 pb-2" style="background:#292929;">
    <div class="col-12 text-left px-4">
      <div class="fullwidth d-flex flex-row justify-content-between">
        <span class="carousel_title">{{ title.toUpperCase() }}</span>
        <div v-if="showMore" @click="viewMore()">
          <span class="carousel_more">MORE</span>
        </div>
      </div>
    </div>
    <div class="col-12 mt-3">
      <div class="fullwidth d-flex flex-row flex-wrap">
        <div
          class="each_box px-2 my-1"
          v-for="item in rails.slice(0, 5)"
          :key="item.id"
          @click="goTo(checkLink(item))"
        >
          <div class="item">
            <div class="item_img">
              <img v-bind:src="item.img_link" @error="imageLoadError($event)" />
            </div>
          </div>
          <p class="items_title pl-1">
            <Trunquee :text=" item.title" />
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import noimage from "../../assets/noimage.png";
import Trunquee from "../trunquee.vue";
import { bus } from "../../main";
export default {
  name: "rails-landscape",
  props: ["rails", "title", "datasource", "cat"],
  data: function() {
    return {
      items: this.rails,
      noimage: noimage,
      showMore: false,
      contenttype: this.datasource
    };
  },
  components: {
    Trunquee
  },
  methods: {
    imageLoadError(event) {
      event.target.src = noimage;
    },
    viewMore() {
      var itemToPass = this.items;
      var contenttype = this.contenttype;
      // const payload = {
      //   source: "VOD",
      //   response: itemToPass
      // };
      // bus.$emit("items_view", payload);

      this.$router.replace(
        "/viewall?cat=" + this.cat + "&contenttype=" + contenttype
      );
    },
    goTo(link) {
      this.$router.push(link);
    },
    checkLink(item) {
      var idSchema, check;
      var id = item.id;
      var title = item.data.title;

      var link;

      if (item.idSchema == "205" || item.idSchema == "206") {
        //vod
        link =
          "/play?scheme=" +
          item.idSchema +
          "&p_type=true&id=" +
          item.id +
          "&eps_id=";
      } else if (item.idSchema == "205") {
        //vod
        link =
          "/play?scheme=" +
          item.idSchema +
          "&p_type=true&id=" +
          item.id +
          "&eps_id=";
      } else if (item.idSchema == "207") {
        //news
        link =
          "/play?scheme=" +
          item.idSchema +
          "&p_type=true&id=" +
          item.id +
          "&eps_id=";
      } else if (item.idSchema == "208") {
        //vod
        link =
          "/play?scheme=" +
          item.idSchema +
          "&p_type=true&id=" +
          item.id +
          "&eps_id=";
      } else if (item.idSchema == "209") {
        //news
        idSchema = "209";
        // console.log(JSON.stringify(item, null, 2));
        link = "/play?scheme=209&p_type=false&id=" + item.id + "&eps_id=";
      } else if (item.idSchema == "203") {
        //news
        idSchema = "209";
        // console.log(JSON.stringify(item, null, 2));
        link = "/podcastplayer?id=" + item.id;
      } else if (idSchema == "204") {
        //pod episode
        link = "/podcastplayer?id=" + item.id;
      }

      return link;
    },
    checkIndexForMore() {
      var listlength = this.items;
      // console.log(JSON.stringify(listlength, null, 2));
      if (listlength.length > 5) {
        this.showMore = true;
      }
    }
  },
  mounted() {
    this.checkIndexForMore();
  }
};
</script>
