      <div class="modal-container">
        <div style="position:relative;">
          <div
            style="position:absolute;top: -40px;right: -40px;cursor:pointer;"
            @click="hide()"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="30"
              height="30"
              viewBox="0 0 24 24"
              fill="none"
              stroke="#f1c40f"
              stroke-width="3"
              stroke-linecap="round"
              stroke-linejoin="round"
            >
              <circle cx="12" cy="12" r="10" />
              <line x1="15" y1="9" x2="9" y2="15" />
              <line x1="9" y1="9" x2="15" y2="15" />
            </svg>
          </div>
          <div class="modal-header">
            <h3 class="header-text">Login</h3>
          </div>

          <div class="modal-body">
            <vue-form :state="formstate" @submit.prevent="onSubmit">
              <validate
                auto-label
                class="form-group required-field"
                :class="fieldClassName(formstate.username)"
              >
                <label class="text-black text-left">Username</label>
                <input
                  type="text"
                  name="username"
                  class="form-control"
                  required
                  v-model.lazy="model.username"
                />

                <field-messages
                  name="username"
                  show="$touched || $submitted"
                  class="form-control-feedback"
                >
                  <div slot="required" class="error">Username is required</div>
                </field-messages>
              </validate>

              <validate
                auto-label
                class="form-group required-field"
                :class="fieldClassName(formstate.password)"
              >
                <label class="text-black text-left">Password</label>
                <input
                  type="password"
                  name="password"
                  class="form-control"
                  required
                  v-model.lazy="model.password"
                />

                <field-messages
                  auto-label
                  name="password"
                  show="$touched || $submitted"
                  class="form-control-feedback"
                >
                  <div slot="required" class="error">
                    Password is required
                  </div>
                  <div slot="password" class="error">
                    Username/Password is invalid.Please try again
                  </div>
                </field-messages>
              </validate>

              <div class="py-2 text-center">
                <button class="modal-default-button" type="submit">
                  Login
                </button>
              </div>
            </vue-form>
            <div class="w-100 bbottom mt-3"></div>
            <div class="w-100 my-2">
              Don't have an account yet?
              <router-link :to="'/signup'" exact @click="hide()"
                >Sign up</router-link
              >
              for free
            </div>
          </div>
        </div>
      </div>
    </div>
    <signup ref="signupPopup" />
