<script>
/* eslint-disable */
// is-active
import RadioService from "../static/json_dummy/radio.json";
import login from "./popup/login.vue";
export default {
  components: { login },
  data: function() {
    return {
      activeid: "",
      filterItem: RadioService.response,
      tv: [],
    };
  },
  created: function() {
    this.setActiveId();
    this.$store.dispatch("fetchLiveTV", { self: this });
  },
  methods: {
    openModal() {
      console.log("openmodal");
      this.$refs.loginPopup.show();
    },
    setActiveId() {
      this.activeid = this.$route.name;
    },
    filterLiveTv() {
      var vm = this;
      vm.tv = this.$store.state.live_tv.data;
      var store = [];
      var imgurl;
      vm.tv.forEach((item) => {
        item["img_link"] = this.imgFiltering(item);
        store.push(item);
      });

      vm.tv = store;
    },
    imgFiltering(item) {
      var imgurl;
      //check if
      var checkThumbnail =
        item.data && item.data.images && item.data.images.thumbnail;
      var checkTitle = item.data && item.data.images && item.data.images.title;

      if (checkThumbnail) {
        imgurl =
          "https://rtb-images.glueapi.io/200x0/" +
          item.data.images.thumbnail.path;
      } else {
        if (checkTitle) {
          imgurl =
            "https://rtb-images.glueapi.io/200x0/" +
            item.data.images.title.path;
        } else {
          imgurl =
            "https://cdn-rtb.glueapi.io/v1/content/" +
            item.id +
            "/image/200x0/images.title";
        }
      }

      return imgurl;
    },
  },
  mounted() {
    this.setActiveId();
  },
};
</script>

