<script>
/* eslint-disable */
// hls plugin for videojs6
// videojs
import Vue from "vue";
import axios from "axios";
import noimage from "../assets/noimage.png";
import social from "../components/popup/sharepopup";
import { bus } from "../main";
// ads
import billboardBannerTop from "../components/ads/billboardBannerTop";
import mrecBannerTop from "../components/ads/mrecBannerTop";
import billboardBannerMiddle from "../components/ads/billboardBannerMiddle";
import mrecBannerMiddle from "../components/ads/mrecBannerMiddle";
import BillboardBannerBottom from "../components/ads/billboardBannerBottom";
import MrecBannerBottom from "../components/ads/mrecBannerBottom";
export default {
  components: {
    social,
    billboardBannerTop: billboardBannerTop,
    mrecBannerTop: mrecBannerTop,
    billboardBannerMiddle: billboardBannerMiddle,
    mrecBannerMiddle: mrecBannerMiddle,
    billboardbannerbottom: BillboardBannerBottom,
    mrecbannerbottom: MrecBannerBottom,
  },
  data: function() {
    return {
      noimage: noimage,
      embed_url: "",
      active_id: "",
      eps_title: "",
      eps_desc: "",
      leftColStyles: {},
      epsColStyles: {},
      pod_episode: [],
      podcast: [],
      window: {
        width: 0,
        height: 0,
      },
      childDataLoaded: false,
      share: [],
      url: "",
      tablet: false,
      mobile: false,
      desktop: false,
      id: this.$route.query.id,
      eps_id: this.$route.query.eps_id,
    };
  },
  created: function() {
    this.$store.dispatch("fetchPodFolder", { self: this });

    this.$nextTick(function() {
      window.addEventListener("resize", this.handleResize);
      this.handleResize();
    });
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
    $("#vidBox").empty();
    // peer5.destroy();
  },
  methods: {
    deviceCheck() {
      if (this.$device.ipad) {
        this.tablet = true;
      } else if (this.$device.mobile) {
        this.mobile = true;
      } else {
        this.desktop = true;
      }
    },
    openModal() {
      this.share = {
        url: window.location.origin + this.$route.fullPath,
        title: this.eps_title,
      };

      bus.$emit("shareVar", this.share);
      this.$refs.social.show();
    },
    imageLoadError(event) {
      event.target.src = noimage;
    },
    goTo(link) {
      this.$router.push(link);
    },
    playVid(items) {
      this.$router.push(
        "podcastplayer?id=" + this.$route.query.id + "&eps_id=" + items.id
      );
    },
    handleResize() {
      this.window.width = window.innerWidth;
      var heightString = this.$refs.vidBox.clientHeight;
      var result = heightString + 70 + "px";
      var eps_result = (result - 70) / 3 + "px";

      if (this.window.width <= "991") {
        Vue.set(this.leftColStyles, "height", "300px");
      } else {
        Vue.set(this.leftColStyles, "height", result);
        Vue.set(this.epsColStyles, "height", eps_result);
      }
    },
    installOwlCarousel: function(carousel) {
      $(carousel).owlCarousel({
        items: 1,
        autoplay: false,
        loop: true,
        nav: true,
        dots: false,
        margin: 10,
        responsiveClass: true,
        responsive: {
          0: {
            items: 1,
            nav: true,
            stagePadding: 0,
          },
          600: {
            items: 2,
            nav: true,
            stagePadding: 0,
          },
          768: {
            items: 5,
            stagePadding: 0,
            loop: false,
          },
        },
      });
    },
    imgFiltering(item) {
      var imgurl;
      //check if
      var checkThumbnail =
        item.data && item.data.images && item.data.images.thumbnail;
      var checkTitle = item.data && item.data.images && item.data.images.title;

      if (checkThumbnail) {
        imgurl =
          "https://rtb-images.glueapi.io/500x0/" +
          item.data.images.thumbnail.path;
      } else {
        if (checkTitle) {
          imgurl =
            "https://rtb-images.glueapi.io/500x0/" +
            item.data.images.title.path;
        } else {
          imgurl =
            "https://cdn-rtb.glueapi.io/v1/content/" +
            item.id +
            "/image/300x0/images.title";
        }
      }

      return imgurl;
    },
    filterPodFolder() {
      // this.podCast = this.$store.state.podcast.data;
      var vm = this;
      vm.podcast = this.$store.state.podcast.data;
      var store = [];
      var imgurl;
      vm.podcast.forEach((item) => {
        item["img_link"] = this.imgFiltering(item);
        store.push(item);
      });
      vm.podcast = store;
      Vue.nextTick(
        function() {
          vm.installOwlCarousel("#podcarousel");
        }.bind(vm)
      );
    },
    setInitialId() {
      this.$router.push("/podcastplayer/" + this.podcast[0].id);
    },
    initializecontentview() {
      var self = this;
      $.ajax({
        type: "POST",
        url: "https://rtb.glueapi.io/report/viewed",
        contentType: "application/json; charset=utf-8",
        data: JSON.stringify({
          idContent: this.active_id, // Content ID from content api response
          contentTitle: this.eps_title, // Content Longtitle from content api response
          site: "RTBGo",
        }),
        dataType: "json",
        success: function(data) {
          // console.log(data);
        },
        failure: function(errMsg) {
          console.log(errMsg);
        },
      });
    },
    initializepageview() {
      var self = this;
      $.ajax({
        type: "POST",
        url: "https://rtb.glueapi.io/report/page-view",
        contentType: "application/json",
        data: JSON.stringify({
          href: this.$route.path,
          title: "podcastplayer",
          site: "RTBGo",
        }),
        dataType: "json",
        success: function(data) {
          // console.log(data);
        },
        failure: function(errMsg) {
          console.log(errMsg);
        },
      });
    },
    PlayerInit(value) {
      var target = document.getElementById("vidBox");
      var gluePlayer = new GluePlyr();
      gluePlayer.setup(value, {
        apiPath: "https://rtb.glueapi.io",
        target: target,
        poster:
          "https://rtb.glueapi.io/v1/content/" +
          value +
          "/image/1200x0/images.title?quality=100", //Replace with the id 413 with the id of the content
        site: "RTBGo",
        type: "audio", // video or audio
        skin: "title",
      });

      window.gluePlayer = gluePlayer;
      this.childDataLoaded = true;

      this.initializecontentview();
      this.initializepageview();
    },

    // ***************** new ****************//
    filterContentData(obj) {
      if (obj.data.podcast) {
        this.setFirstIditems(obj);
        this.getDirectContentData(obj.data.podcast.id);
      }

      if (obj.data["podcast-episode"]) {
        this.getEpsListContentData(obj.data["podcast-episode"]);
      }
    },

    async getEpsListContentData(list) {
      let vm = this;
      let promises = [];

      list.forEach(function(items) {
        const myUrl = `https://cdn-rtb.glueapi.io/v1/content/${
          items.id
        }?format=json`;
        promises.push(axios.get(myUrl));
      });

      Promise.all(promises).then(function(results) {
        results.forEach(function(response, index) {
          const res = response.data.data;
          if (res) {
            vm.pod_episode.push(response.data.data);
          }

          if (index == results.length - 1) {
            vm.pod_episode.sort(function(a, b) {
              return b.id - a.id;
            });

            if (vm.id && !vm.eps_id) {
              vm.setFirstIditems(vm.pod_episode[0]);
            }
          }
        });
      });
    },

    setFirstIditems(obj) {
      this.eps_title = obj.longTitle;
      this.eps_desc = obj.data.description;
      this.active_id = obj.id;
      this.embed_url =
        "https://rtb-player.glueapi.io/latest/p/" + obj.id + "?skin=title";
      this.PlayerInit(obj.id);

      if (this.active_id && this.eps_desc) {
        this.initializecontentview();
        this.initializepageview();
      }
    },

    async getDirectContentData(value) {
      axios
        .get(`https://cdn-rtb.glueapi.io/v1/content/${value}?format=json`)
        .then((response) => {
          this.filterContentData(response.data.data);
        });
    },

    async initializeContentForStart() {
      if (this.eps_id) {
        this.getDirectContentData(this.eps_id);
      } else {
        this.getDirectContentData(this.id);
      }
      //first,check for eps_id,klu ada,direct call ke exisiting id
      //klu xde,extract
    },
  },
  mounted() {
    this.deviceCheck();
    this.$nextTick(function() {
      this.handleResize();
    });
  },
  beforeMount() {
    this.initializeContentForStart();
  },
  computed: {
    initPeer() {
      // Our fancy notification (2).
      // peer5
      // peer5.destroy();
      // peer5.init();
      // peer5.isInitialized();
    },
  },
};
</script>

