  <div class="mb_controller">
    <div class="bg p-0" style="width:100%;">
      <div class="col-md-12">
        <div class="row bg_header d-flex justify-content-center align-items-center">
          <div class="channel_logo">
            <span class="bg_text">{{ titlesofview.toUpperCase() }}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="row pt-3 pb-2" style="background:#313131;">
        <div class="col-12 text-left px-4">
          <div class="fullwidth d-flex flex-row justify-content-between">
            <span class="carousel_title" v-if="categoryTitle == 'nostalgic'">
              NOSTALGIA
            </span>
            <span class="carousel_title" v-else>
              {{
              categoryTitle.toUpperCase()
              }}
            </span>
            <div @click="goTo(backlink)">
              <span class="carousel_more">BACK</span>
            </div>
          </div>
        </div>
      </div>
      <div class="row pb-2" style="background:#313131;">
        <div class="col-12 mt-3">
          <div class="fullwidth d-flex flex-row flex-wrap">
            <div
              class="each_box px-2 my-1"
              v-for="item in vod_each"
              :key="item.id"
              @click="goTo(checkLink(item))"
            >
              <div class="item">
                <div class="item_img">
                  <img v-bind:src="item.img_link" @error="imageLoadError($event)" />
                </div>
              </div>
              <p class="items_title pl-1">
                <Trunquee :text=" item.title" />
              </p>
            </div>
          </div>
        </div>
      </div>

      <!-- ads -->
      <!-- <div class="row my-2 pb-3 d-flex justify-content-center align-items-center">
        <div class="col-12 col-md-10 col-lg-8 col-xl-7">
          <div class="full_width justify-content-center banner_mnn d-none d-sm-flex">
            <img src="../assets/banner/promo_d.jpg" class="full_width" />
          </div>
          <div class="full_width justify-content-center banner_mnn d-flex d-sm-none">
