<script>
/* eslint-disable */
import noimage from "../assets/noimage.png";
import { bus } from "../main";
export default {
  name: "viewall",
  data: function() {
    return {
      vod: [],
      vod_each: [],
      news: [],
      specials: [],
      podcast: [],
      titlesofview: this.$route.query.contenttype,
      categoryTitle: this.$route.query.cat,
      backlink: ""
    };
  },
  methods: {
    imageLoadError(event) {
      event.target.src = noimage;
    },
    goTo(link) {
      this.$router.push(link);
    },
    checkLink(item) {
      var idSchema, check;
      var id = item.id;
      var title = item.data.title;

      var link;

      if (item.idSchema == "205" || item.idSchema == "206") {
        //vod
        link =
          "/play?scheme=" +
          item.idSchema +
          "&p_type=true&id=" +
          item.id +
          "&eps_id=";
      } else if (item.idSchema == "205") {
        //vod
        link =
          "/play?scheme=" +
          item.idSchema +
          "&p_type=true&id=" +
          item.id +
          "&eps_id=";
      } else if (item.idSchema == "207") {
        //news
        link =
          "/play?scheme=" +
          item.idSchema +
          "&p_type=true&id=" +
          item.id +
          "&eps_id=";
      } else if (item.idSchema == "208") {
        //vod
        link =
          "/play?scheme=" +
          item.idSchema +
          "&p_type=true&id=" +
          item.id +
          "&eps_id=";
      } else if (item.idSchema == "209") {
        //news
        idSchema = "209";
        // console.log(JSON.stringify(item, null, 2));
        link = "/play?scheme=209&p_type=false&id=" + item.id + "&eps_id=";
      } else if (item.idSchema == "203") {
        //news
        idSchema = "209";
        // console.log(JSON.stringify(item, null, 2));
        link = "/podcastplayer?id=" + item.id;
      } else if (idSchema == "204") {
        //pod episode
        link = "/podcastplayer?id=" + item.id;
      }

      return link;
    },
    initPage() {
      // console.log(this.titlesofview);
    },
    imgFiltering(item) {
      var imgurl;
      //check if
      var checkThumbnail =
        item.data && item.data.images && item.data.images.thumbnail;
      var checkTitle = item.data && item.data.images && item.data.images.title;
      var checkImgFeature =
        item.data && item.data.images && item.data.images.feature;

      if (checkThumbnail) {
        imgurl =
          "https://rtb-images.glueapi.io/500x0/" +
          item.data.images.thumbnail.path;
      } else {
        if (checkTitle) {
          imgurl =
            "https://rtb-images.glueapi.io/500x0/" +
            item.data.images.title.path;
        } else {
          if (checkImgFeature) {
            imgurl =
              "https://rtb-images.glueapi.io/500x0/" +
              item.data.images.feature.path;
          } else {
            imgurl =
              "https://cdn-rtb.glueapi.io/v1/content/" +
              item.id +
              "/image/300x0/images.title";
          }
        }
      }
      return imgurl;
    },
    filterNews() {
      var vm = this;
      vm.news = this.$store.state.news_eps.data;

      var store = [];
      var imgurl;
      vm.news.forEach(item => {
        item["img_link"] = this.imgFiltering(item);
        store.push(item);
      });
      vm.news = store;
    },
    filterPodFolder() {
      // this.podCast = this.$store.state.podcast.data;
      var vm = this;
      vm.podcast = this.$store.state.podcast.data;
      var store = [];
      var imgurl;
      vm.podcast.forEach(item => {
        item["img_link"] = this.imgFiltering(item);
        store.push(item);
      });
      vm.podcast = store;
    },
    filterVodMixedFolder() {
      var vm = this;
      vm.vod = this.$store.state.vodmixednews.data;

      var store = [];
      var imgurl;
      vm.vod.forEach(item => {
        item["img_link"] = this.imgFiltering(item);
        store.push(item);
      });
      vm.vod = store;
    },
    filterSpecials() {
      var vm = this;
      vm.specials = this.$store.state.special.data;
      var store = [];
      var imgurl;
      vm.specials.forEach(item => {
        item["img_link"] = this.imgFiltering(item);
        store.push(item);
      });

      vm.specials = store;
    },
    filterBasedOnCategory() {
      var obj = [];
      if (this.titlesofview === "vod") {
        obj = this.vod;
      } else if (this.titlesofview === "podcast") {
        obj = this.podcast;
      } else if (this.titlesofview === "news") {
        obj = this.news;
      }

      var each = [];

      this.vod_each;
      obj.forEach(items => {
        var content_type;
        if (this.titlesofview == "vod") {
          content_type = items.data.content_type;
          this.backlink = "/vod";
        } else if (this.titlesofview == "podcast") {
          content_type = items.data.category;
          this.backlink = "/podcast";
        } else if (this.titlesofview == "news") {
          content_type = items.data.content_type;
          this.backlink = "/vod";
        }

        // console.log(JSON.stringify(contenttype, null, 2));

        var categories = this.$route.query.cat;
        if (content_type.length != 0) {
          content_type.forEach(items_cat => {
            if (
              categories === items_cat.title.en_US ||
              categories === this.convertStringToLower(items_cat.title.en_US) ||
              categories ===
                this.convertStringToLower(
                  items_cat.title.en_US.replace(/\s/g, "")
                )
            ) {
              this.vod_each.push(items);
            }
          });
        }
      });
    },
    convertStringToLower(items) {
      var strings;
      strings = items.toLowerCase();
      return strings;
    }
  },
  created: function() {
    this.$store.dispatch("fetchVodMixedFolder", { self: this });
    this.$store.dispatch("fetchPodFolder", { self: this });
    this.$store.dispatch("fetchNews", { self: this });
    this.$store.dispatch("fetchSpecials", { self: this });
    this.initPage();
  },
  mounted() {
    setTimeout(
      function() {
        this.filterBasedOnCategory();
      }.bind(this),
      1200
    );
  }
};
</script>

